.startScreen {
    width: 100%;
    height: calc(100vh - 80px);

    padding: 10px;

    background-color: #112D32;
    color: #88BDBC;
    text-align: center;
}

.heading {
    margin-top: 250px;

    font-size: 5em;
}

.subHeading {
    margin-top: 20px;
    font-size: 2em;
}

.socials {
    margin-top: 100px;
}

.icon {
    margin: 0 20px;

    font-size: 3em;
}
a:hover .icon {
    color: #88BDBC;
}

a:active .icon {
    color: #254E58;
}